@import "./mixins";
@import "./variables";

.excel__table {
  position: absolute;
  right: 0;
  top: $header-height + $toolbar-height + $formula-height;
  left: 0;
  overflow-x: auto;
  padding-bottom: 1px;


  .row {
    display: flex;
    flex-direction: row;
    min-height: 20px;
    height: $row-height;
  }

  .row-info {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: $info-cell-width;
    height: 100%;
    border: 1px solid $border-color;
    background: #f8f9fa;
    border-top: none;
  }

  .row-data {
    display: flex;

  }

  .column{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f8f9fa;
    min-width: 40px;
    width: $cell-width;
    border: 1px solid $border-color;
    height: 100%;
    border-top: 0;
    border-left: 0;
  }

  .cell {
    min-width: 40px;
    padding: 5px;
    width: $cell-width;
    border: 1px solid #e2e3e3;
    border-left: 0;
    border-top: 0;
    color: #111;
    white-space: nowrap;
    outline: none;
    &.selected {
      outline: 1px solid $primary-color;
      border: none;
      z-index: 2;
    }
  }

  .row-resize, .col-resize{
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0;
    background-color: $primary-color;
    z-index: 999;
    &:hover{
      opacity: 1 !important;
    }
  }

  .col-resize{
    top: 0;
    width: 2px;

    &:hover{
      cursor: col-resize;
    }
  }

  .row-resize{
    left: 0;
    height: 4px;

    &:hover{
      cursor: row-resize;
    }
  }
}