@import "./mixins";
@import "./variables";

.excel__toolbar{
	position: absolute;
  height: $toolbar-height;
  left: 0;
  right: 0;
  top: $header-height;
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  padding: 7px 10px;
  display: flex;

  .button{
    @include button(green)
  }
}